import React,{Component} from 'react';
import { Collapse } from 'react-bootstrap';

class ProductDetailDropdownContent extends Component {
    
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.state = { collapse: false };
    if(this.props.index==0){
        this.state.collapse =true;
    }
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  render(){
      const { header, text } = this.props;
      console.log('123');
      return (
        <div className={'bo6 p-t-15 p-b-14'} onClick={this.toggle} aria-expanded={this.state.collapse}>
            <h5 className="flex-sb-m cs-pointer m-text19 color0-hov">
                {header}
            </h5>
            <Collapse in={this.state.collapse}>
                <div className={'p-t-3'}>
                    <p className="">
                        {text}
                    </p>
                </div>
            </Collapse>
        </div>
      );
  }
};

export default ProductDetailDropdownContent;